// SLOGAN-BLOCK
.slogan-block {
    @include lg() {
        margin-top: 8.6rem;
        margin-bottom: 8.6rem;
        align-items: center; }
    @include sm() {
        flex-wrap: wrap;
        margin-top: (8.6rem/1.5);
        margin-bottom: (8.6rem/1.3); }
    @include xs() {
        margin-top: (8.6rem/2.5);
        margin-bottom: (8.6rem/2); }
    @include wrapper();
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 8.6rem;
    margin-bottom: 8.6rem;
    position: relative;
    z-index: 10;
 }    // overflow: hidden

.slogan-left-side {
    @include sm() {
        flex-basis: 100%;
        margin-right: 0;
        br {
            display: none; } }
    flex-basis: 50%;
    margin-right: 1.5rem;

    &__h2 {
        @include lg() {
            font-size: (6.4rem/1.1); }
        @include md() {
            font-size: (6.4rem/1.5); }
        @include sm() {
