        margin-bottom: (6.6rem/1.5); }
    @include xs() {
        margin-top: (2rem/2);
        margin-bottom: (6.6rem/2); }
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 6.6rem;
    line-height: 1;

