// SLIDER
.owl-carousel {
    @include lg() {
        margin-bottom: (6.4rem/1.5); }
    @include xs() {
        margin-bottom: (6.4rem/2); }
    max-width: 109rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    margin: 0 auto;
    margin-bottom: 6.4rem; }

.owl-dots {
    @include lg() {
        display: block;
        margin-top: 1rem; }
    display: none; }

.owl-nav {
    @include lg() {
        display: none; }
    position: absolute;
    bottom: 50%;
    transform: translateY(50%);
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; }

.tmla-handle {
    display: none; }

.owl-prev span {
    @include lg() {
        display: none; }
    color: $accent;
    font-size: 10rem;
    line-height: .4;
    position: absolute;
    left: -2rem;
    top: 47.5%;
    transform: translateY(-50%); }

.owl-next span {
    @include lg() {
        display: none; }
    color: $accent;
    font-size: 100px;
    line-height: .4;
    position: absolute;
    right: -20px;
    top: 47.5%;
    transform: translateY(-50%); }

.item {
    width: 100%;
    padding-bottom: 56.25%; // 16:9
    // padding-bottom: 75% // 3:4
    position: relative;
    background-color: #000;

    &:nth-child(odd) {
        margin-bottom: 2rem; }

    &__play-button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 10rem;
        height: 10rem;
        z-index: 10;
        opacity: .3;
        fill: $white-color;
        transition: opacity .6s linear;
        cursor: pointer;

        &:hover {
            opacity: .9; } }

    video {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        // filter: saturate(1%)
        filter: grayscale(100%);
        transition: filter 1.5s linear; } }
// END SLIDER

