//BURGER
.burger {
    @include sm() {
        display: block; }
    position: absolute;
    top: 0;
    left: 0;
    margin: 2rem 0 0 2rem;
    width: 56px;
    height: 56px;
    display: none;


    &__span {
        display: block;
        position: relative;
        width: 100%;
        height: 4px;
        background-color: $burger-color;
        transition: all 0.3s;
        backface-visibility: hidden;
        border-radius: 2px;

        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 32px;
            width: 100%;
            height: 4px;
            background-color: $burger-color;
            transition: all 0.3s;
            backface-visibility: hidden;
            border-radius: 2px; }

        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 16px;
            width: 100%;
            height: 4px;
            background-color: $burger-color;
            transition: all 0.3s;
            backface-visibility: hidden;
            border-radius: 2px; } } }

//END BURGER


// MOBILE MENU (SWIPED)
