.wrapper {
    @include wrapper(); }

@keyframes mobile-opacity {
    0% {
        opacity: .05; }

    50% {
        opacity: 1; }

    100% {
        opacity: .05; } }

@keyframes mobile-scale {
    0% {
        transform: scale(1); }

    50% {
        transform: scale(1.5); }

    100% {
        transform: scale(1); } }

@keyframes m {
    0% {
        background-position: 0% 50%; }

    50% {
        background-position: 100% 50%; }

    100% {
        background-position: 0% 50%; } }

@keyframes m2 {
    0% {
        transform: scaleY(1); }

    50% {
        transform: scaleY(1.8); }

    100% {
        transform: scaleY(1); } }

@keyframes m3 {
    0% {
        transform: scale(1); }

    50% {
        transform: scale(1.5); }

    100% {
        transform: scale(1); } }

@keyframes blend {
    0% {
        background-blend-mode: normal; }

    100% {
        background-blend-mode: difference; } }

@keyframes blur {
    0% {
        filter: blur(0px); }

    10% {
        filter: blur(10px); }

    100% {
        filter: blur(0px); } }


//HEADER
.header {
    @include lg() {
        min-height: auto; }
    @include md() {
        min-height: 100vh; }
    @include xs() {
        padding-bottom: (8rem/2); }
    position: relative;
    width: 100%;
    overflow: hidden;
    padding-bottom: 8rem;
    min-height: 100vh;
    // overflow: hidden

    &::after {
        @include sm() {
            will-change: opacity;
            animation: mobile-opacity 15s infinite;
            animation-timing-function: linear;
            // filter: blur(2px)
            // transform: scale(1)
            background: $bg-color url('../img/background/team-image-2.png') no-repeat scroll center;
            background-size: cover;
            // transform: skew(40deg) scale(1.2)
 }            // animation: mobile-scale 10s infinite
        content: ' ';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: $bg-color url('../img/background/team-image.png') no-repeat fixed center;
        background-size: cover;
        z-index: -20;
        opacity: .7; } }

//HEADER-VIDEO
.header-video {
    @include md() {
        width: auto;
        transform: translate(-43%);
        left: 50%;
        height: 100%; }

    //TODO: В КОНЦЕ ПРОВЕРЯЮ НА СКОЛЬКО БЫСТРО ГРУЗИТСЯ НА ТЕЛЕФОНАХ
    // +md()
    //     display: none
    position: absolute;
    width: 100%;
    left: 0;//TODO: определяю нужно ли мне видео на телефонах если нет то меняю видз на 100;
    top: 0;
    opacity: .4;
    z-index: -10; }
//END HEADER-VIDEO

//HEADER-TOP
.header-top {
    @include md() {
        justify-content: center;
        flex-wrap: nowrap; }
    @include xs() {
        margin-bottom: (13.4rem/3); }
    @include xxs() {
        margin-bottom: (13.4rem/3.5); }
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    padding-top: 3.4rem;
    margin-bottom: 13.4rem;

    &__logo {
        @include md() {
            margin-right: (13.8rem/3); }
        @include sm() {
            margin-right: 0;
            width: 10.8rem;
            height: 10.8rem; }
        @include xs() {
            width: (10.8rem/1.3);
            height: (10.8rem/1.3); }
        width: 4.9rem;
        height: 4.9rem;
        margin-right: 13.8rem; }

    &__logo img {
        @include sm() {
            width: 10.8rem;
            height: 10.8rem; }
        @include xs() {
            width: (10.8rem/1.3);
            height: (10.8rem/1.3); }
        width: 4.9rem;
        height: 4.9rem; }

    &__link {
        @include md() {
            margin-right: (4.7rem/3); }
        @include sm() {
            display: none; }
        color: $text-color;
        text-decoration: none;
        text-transform: uppercase;
        font-size: $menu-link-fz;
        line-height: 3.6rem;
        margin-right: 4.7rem;
        transition: $color-tr-ease-in-out;

        &:hover {
            color: $accent; }

        &:last-child {
            @include md() {
                margin-right: (22.8rem/3); }
            margin-right: 22.8rem; } }

    &__quote {
        @include sm() {
            display: none; }
        color: $accent;
        font-weight: 700;
        font-size: $header-quote-fz;
        text-transform: uppercase;
        line-height: 3.6rem;
        cursor: pointer;
        transition: $color-tr-ease-in-out;

        &:hover {
            color: $text-color; } } }
//END HEADER-TOP

//HEADER-MIDDLE
.header-middle {
    @include md() {
        justify-content: center;
        flex-wrap: nowrap; }
    @include sm() {
        flex-wrap: wrap; }
    @include xs() {
        margin-bottom: (16rem/2); }
    @include xxs() {
        margin-bottom: (16rem/3.8); }
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 16rem;

    &__title {
        @include md() {
            margin-right: 13.2rem; }
        @include sm() {
            width: 100%;
            margin-right: 0;
            margin-bottom: 13.4rem; }
        @include xs() {
            margin-bottom: (13.4rem/3); }
        padding: 2.1rem 6.8rem 2.1rem 0;
        margin-right: 12.4rem;
        color: $white-color;
        position: relative;


        // &.red::after
        //     width: 80%

        // &::after
        //     position: absolute
        //     content: ' '
        //     top: 0
        //     left: -100%
        //     background: $accent
        //     width: 200%
        //     height: 100%
 }        //     z-index: -1

    &__h1 {
        @include md() {
            width: 100%;
            font-size: ($header-title-fz/2); }
        @include xs() {
            font-size: ($header-title-fz/2.5); }
        @include xxs() {
            font-size: ($header-title-fz/3.1); }
        font-size: $header-title-fz;
        font-weight: 900;
        line-height: .96;
        width: 0%;
        // transform: scaleX(0)
        transform-origin: left;
        overflow: hidden;
        white-space: nowrap;
        position: relative;
        z-index: 1; }


    &__text {
        @include md() {
            font-size: ($header-subtitle-fz/2); }
        @include xs() {
            font-size: ($header-subtitle-fz/2.5); }
        @include xxs() {
            font-size: ($header-subtitle-fz/3.1); }
        font-size: $header-subtitle-fz;
        font-weight: 300;
        line-height: .96;
        clip: rect(40px 250px 160px 50px);
        position: relative;
        z-index: 1; }


    &__see-work {
        @include md() {
            font-size:($header-see-work-fz/1.5) {}
            padding: ((7.6rem/1.63) (3.2rem/1.63)); }
        @include xs() {
            font-size:($header-see-work-fz/1.7) {}
            padding: ((7.6rem/1.88) (3.2rem/1.98)); }
        font-size: $header-see-work-fz;
        font-weight: 500;
        line-height: 1;
        text-transform: uppercase;
        color: $white-color;
        background: #000;
        padding: 7.6rem 3.2rem;
        border-radius: 50%;
        cursor: pointer;
        // opacity: 0

        a {
            color: $white-color;
            text-decoration: none; } }

    &__empty-block {
        @include md() {
            height: (17.1rem/1.6);
            width: (5.4rem/1.6); }
        @include sm() {
            display: none; }
        background: $accent;
        height: 17.1rem;
        width: 5.4rem;
        position: absolute;
        right: 0;
        transform: scaleX(1);
        transform-origin: left;

        &:hover {
            transform: scaleX(2); } } }
//END HEADER-MIDDLE

//HEADER-CONTACT
.header-contact {
    @include md() {
        justify-content: center;
        flex-wrap: nowrap; }
    @include sm() {
        flex-wrap: wrap; }
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    // padding-bottom: 8rem

    &__social {
        @include md() {
            margin-right: (13rem/2.6); }
        @include sm() {
            width: 100%;
            margin-right: 0;
            display: flex;
            justify-content: flex-start;
            margin-bottom: 1rem; }
        margin-right: 13rem; }

    &__social a {
        @include md() {
            font-size: ($header-social-fz / 1.36); }
        @include sm() {
            font-size: $header-social-fz; }
        color: $text-color;
        text-decoration: none;
        font-size: $header-social-fz;
        transition: $color-tr-ease-in-out;

        &:hover {
            color: $accent; }

        &:nth-child(1) {
            @include md() {
                margin-right: (2.9rem/2.6); }
            margin-right: 2.9rem; } }

    &__email {
        @include sm() {
            width: 100%;
            margin-right: 0;
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
            margin-bottom: 2rem; } }

    &__email a {
        @include md() {
            font-size: ($header-email-fz / 1.36); }
        @include sm() {
            font-size: $header-email-fz; }
        color: $text-color;
        text-decoration: none;
        font-size: $header-email-fz;
        transition: $color-tr-ease-in-out;


        &:hover {
            color: $accent; }

        &:first-child {
            @include md() {
                margin-right: (2.4rem/2.6); }
            @include sm() {
                margin-right: 2.4rem; }
            margin-right: 2.4rem; }

        &:last-child {
            @include md() {
                margin-right: (13.6rem/2.6); }
            @include sm() {
                margin-right: 0; }
            margin-right: 24.6rem; } }

    &__phone {
        @include sm() {
            width: 100%;
            display: flex;
            justify-content: flex-start; } }

    &__phone a {
        @include md() {
            font-size: ($header-phone-fz / 1.36); }
        @include sm() {
            font-size: $header-phone-fz; }
        color: $text-color;
        text-decoration: none;
        font-size: $header-phone-fz;
        transition: $color-tr-ease-in-out;

        &:hover {
            color: $accent; } } }
//END HEADER-CONTACT
//END HEADER


.green-block {
    @include sm() {
        transform: none !important; }

    @include md() {
        left: -40%;
        width: 110%; }
    position: absolute;
    top: 0;
    left: -20%;
    background: $accent;
    // transform: translate3d(-50%, 0, 0)
    width: 100%;
    height: 100%;
    transform: scaleX(0);
    transform-origin: left; }

